.page-footer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-self: flex-end;
}

.page-footer__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  // align-items: flex-start;
  // min-width: 1454px;
  margin: 0 auto;
  padding: 66px 19px 60px 26px;

  @media (max-width: $desktop-width) {
    flex-direction: column;
    // max-width: 1024px;
    margin: 0;
    padding: 0;
  }
}

.page-footer__links-wrap {
  margin: 0;

  @media (max-width: $desktop-width) {
    position: relative;
    width: 100%;

    &::before {
      position: absolute;
      content: "";
      bottom: -3px;
      width: 100%;
      height: 1px;
      border: 1px solid $border-gray;
      opacity: 0.2;
    }
  }

  @media (max-width: $tablet-width) {
    &::before {
      border: none;
    }
  }
}

.page-footer__links {
  // max-width: 250px;
  // margin: 0 200px 0 auto;
  padding: 0;

  @media (max-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    // max-width: 770px;
    align-items: center;
    max-height: 117px;
    padding: 22px 20px 21px 24px;
  }

  @media (max-width: $tablet-width) {
    flex-direction: column;
    // max-width: 320px;
    padding-top: 45px;
    padding-left: 34px;
  }
}

.page-footer__links img {
  margin-bottom: 51px;

  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.6;
  }

  @media (max-width: $desktop-width) {
    margin-bottom: 0;
  }

  @media (max-width: $tablet-width) {
    width: 225px;
    margin-bottom: 21px;
    padding-left: 7px;
  }
}

.page-footer__links svg {
  width: 216px;
  height: 52px;
  margin-bottom: 47px;

  @media (max-width: $desktop-width) {
    margin-bottom: 0;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 23px;
  }
}

.page-footer__links p {
  display: none;
  margin: 0;
  margin-bottom: 32px;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  color: $gray-darkest;
  opacity: 0.5;
  letter-spacing: 0.44px;

  @media (max-width: $desktop-width) {
    display: block;
    margin-bottom: 0;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 31px;
  }
}

.page-footer__social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 111px;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: $desktop-width) {
    align-items: inherit;
    min-width: 112px;
  }

  @media (max-width: $tablet-width) {
    margin: 0 auto;
  }
}

.page-footer__link {
  &:hover {
    opacity: 0.4;
  }

  &:active {
    opacity: 0.8;
  }
}

.page-footer__link svg {
  width: 27px;
  height: 27px;
}

.page-footer__accordion-wrap {
  display: flex;
  flex-direction: row;
  // min-width: 990px;

  justify-content: space-between;

  @media (max-width: $desktop-width) {
    flex-direction: row;
    // min-width: 770px;
    padding: 40px 37px 37px 23px;
  }

  @media (max-width: $tablet-width) {
    position: relative;
    flex-direction: column;
    padding: 0;
    padding-top: 146px;
  }
}

.page-footer__menu {
  @media (max-width: $desktop-width) {
    margin-right: 65px;
  }

  @media (max-width: $tablet-width) {
    margin-right: 0;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      top: -2px;
      width: 100%;
      height: 1px;
      border-top: 1px solid $border-gray;
    }

    &::before {
      position: absolute;
      content: "";
      bottom: -3px;
      width: 100%;
      height: 1px;
      border-bottom: 1px solid $border-gray;
    }
  }
}

.page-footer__list {
  margin: 0 78px 0 0;
  padding: 0;
  list-style: none;
}

.page-footer__list:nth-child(2n) {
  margin-right: 0;
}

// .page-footer__item {
//   @media (max-width: $tablet-width) {
//     padding-left: 15px;
//     padding-right: 20px;
//   }
// }

.page-footer__item a {
  font-size: 15px;
  line-height: 25px;
  color: $gray-darkest;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 0.5;
  }
}

