.page-footer__contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-footer__contacts-item a {
  font-size: 15px;
  line-height: 25px;
  color: $gray-darkest;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 0.5;
  }
}

.page-footer__bottom {
  border-top: 1px solid $border-gray;
  top: -18px;

  @media (max-width: $desktop-width) {
    top: 17px;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 14px auto;
  padding-left: 30px;
  font-size: 13px;
  line-height: 24px;
  align-items: center;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $gray-darkest;
  opacity: 0.5;

  @media (max-width: $desktop-width) {
    max-width: 762px;
    margin: 21px 0 22px;
    padding-left: 24px;
    padding-right: 20px;
  }

  @media (max-width: $tablet-width) {
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    // max-width: 320px;
    padding-top: 29px;
  }
}

.page-footer__wrapper-copyright {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  margin: 0;
  margin-right: 143px;

  @media (max-width: $desktop-width-max) {
    margin-right: 43px;
  }

  @media (max-width: $desktop-width) {
    display: none;
  }
}

.page-footer__wrapper-copyright span {
  margin-right: 69px;
}

.page-footer__wrapper a {
  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.6;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 21px;
  }
}

.page-footer__wrapper-author {
  margin: 0;
  margin-left: auto;

  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.6;
  }

  @media (max-width: $tablet-width) {
    margin: 0 auto;
  }
}
